.course-content-container {
  max-width: 900px;
  margin: 0 auto;
  padding: 40px 20px;
  font-family: 'Roboto', sans-serif;
}

.course-header {
  margin-bottom: 30px;
  border-bottom: 2px solid #eee;
  padding-bottom: 20px;
}

.btn-back {
  background: none;
  border: none;
  color: var(--primary-color);
  font-weight: 600;
  padding: 0;
  margin-bottom: 15px;
}

.course-header h1 {
  font-size: 28px;
  color: var(--sidebar-bg);
  margin: 0 0 10px 0;
}

.progress-label {
  font-size: 14px;
  color: var(--text-secondary);
  font-weight: 600;
}

.progress-info {
  display: flex;
  gap: 20px;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 10px;
}

.lesson-count,
.required-hours {
  font-size: 14px;
  color: var(--text-secondary);
}

/* Save Status Indicator */
.save-status {
  font-size: 13px;
  font-weight: 500;
  padding: 4px 12px;
  border-radius: 4px;
  display: inline-flex;
  align-items: center;
  gap: 5px;
}

.save-status.saving {
  background: #e3f2fd;
  color: #1976d2;
}

.save-status.saved {
  background: #E8F5E9;
  color: #2E7D32;
}

.save-status.error {
  background: #FFEBEE;
  color: #C62828;
}

/* Content Card */
.content-card {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  border: 1px solid #e1e8ed;
  overflow: hidden;
}

.card-header {
  background: #f8fafc;
  padding: 20px 30px;
  border-bottom: 1px solid #eee;
}

.card-header h2 {
  font-size: 20px;
  margin: 0;
  color: var(--sidebar-bg);
}

.card-body {
  padding: 30px;
}

.training-text {
  line-height: 1.8;
  font-size: 16px;
  color: #444;
}

.training-text h3 {
  color: var(--sidebar-bg);
  margin-top: 25px;
}

.training-text p {
  margin-bottom: 15px;
}

/* Questions & Options */
.question-block {
  margin-bottom: 30px;
  padding-bottom: 20px;
  border-bottom: 1px dashed #eee;
}

.question-text {
  font-size: 17px;
  font-weight: 500;
  margin-bottom: 15px;
}

.options-grid {
  display: grid;
  gap: 12px;
}

.option-item {
  padding: 15px 20px;
  border: 2px solid #edf2f7;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s;
  background: #fdfdfd;
}

.option-item:hover {
  border-color: var(--primary-color);
  background: #f7fafc;
}

.option-item.selected {
  border-color: var(--primary-color);
  background: var(--primary-light);
  font-weight: 600;
}

/* Actions */
.card-actions {
  padding: 20px 30px;
  background: #f8fafc;
  display: flex;
  justify-content: flex-end;
}

/* Success State */
.success-card {
  text-align: center;
  padding: 50px 20px;
}

.success-icon {
  font-size: 60px;
  margin-bottom: 20px;
}

.final-score {
  font-size: 24px;
  font-weight: 700;
  color: var(--primary-color);
  margin: 20px 0;
}

.text-center {
  text-align: center;
}